import { VueConstructor } from "vue";
import {
    Layout,
    Icon,
    Menu,
    Drawer,
    Row,
    Col,
    List,
    Avatar
} from "ant-design-vue";

const components = [Layout, Icon, Menu, Drawer, Row, Col, List, Avatar];

export default function(Vue: VueConstructor, options?: any) {
    components.forEach(component => {
        Vue.use(component);
    });
}
