const mutations = {
    // @ts-ignore
    PLAY(state, payload) {
        state.playing = payload;
    }
};

const getters = {
    list: (state: any) => state.list,
    playing2: (state: any) => state.playing
};

const actions = {
    // @ts-ignore
    play({ commit, state }, _song: any) {
        // @ts-ignore
        const { song, artist } = _song;
        state.list.forEach((element: any) => {
            if (element.name === song && element.artist === artist) {
                commit("PLAY", element);
            }
        });
        // const _song = state.list.matched((item: any) => item.name === name && item.artist === artist);
        // commit("PLAY", _song);
    }
};

export default {
    state,
    mutations,
    getters,
    actions
};
