














































import { Vue, Component, PropSync, Prop } from "vue-property-decorator";
import { Layout, Drawer } from "ant-design-vue";

const { Sider } = Layout;

@Component({
    // @ts-ignore
    components: {
        "a-drawer": Drawer,
        "a-layout-sider": Sider
    },
    mq: {
        tablet: "(max-width: 768px)"
    }
})
export default class DrawerCustom extends Vue {
    @PropSync("collapsed", {
        type: Boolean,
        required: true
    })
    private collapsedPropSync!: boolean;

    @Prop({
        type: String,
        default: "light"
    })
    private readonly menuTheme!: boolean;

    @Prop({
        type: Array,
        required: true
    })
    private readonly defaultSelectedKeys!: any[];

    @Prop({
        type: Array,
        required: true
    })
    private readonly listMenu!: Object[];
}
