



















import { Vue, Component } from "vue-property-decorator";
import { Divider } from "ant-design-vue";
import { mapGetters, mapActions } from "vuex";

@Component({
    components: {
        Divider
    },
    computed: {
        ...mapGetters(["list"])
    },
    methods: {
        ...mapActions(["play"])
    }
})
export default class Home extends Vue {
    getTime() {
        const sayings = ["Good morning", "Good afternoon", "Good everning"];

        const hour = new Date().getHours();

        if (hour >= 18 || hour < 5) {
            return sayings[2];
        } else if (hour >= 5 && hour < 12) {
            return sayings[0];
        } else {
            return sayings[1];
        }
    }

    playMusic(song: string, artist: string) {
        const obj = { song, artist };
        console.log(obj);
        // @ts-ignore
        this.play(obj);
    }
}
