








import { Vue, Component, PropSync } from "vue-property-decorator";
import { Icon } from "ant-design-vue";

@Component({
    name: "toggle-menu-icon",
    components: {
        Icon
    }
})
export default class ToggleMenuIcon extends Vue {
    @PropSync("collapsed", {
        type: Boolean,
        required: true
    })
    private collapsedPropSync!: boolean;
}
